.App {
  text-align: center;
}

.App-header {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.intro-container {
  max-width: 800px;
  padding: 40px;
  animation: fadeIn 0.8s ease-in;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.intro-name {
  font-size: 4rem;
  font-weight: 700;
  margin: 0 0 10px 0;
  letter-spacing: -1px;
}

.intro-major {
  font-size: 1.5rem;
  font-weight: 300;
  margin: 0 0 30px 0;
  opacity: 0.9;
}

.intro-hook {
  font-size: 1.2rem;
  font-weight: 400;
  max-width: 600px;
  margin: 0 auto 40px;
  line-height: 1.6;
  opacity: 0.95;
}

.intro-cta {
  display: flex;
  gap: 20px;
  justify-content: center;
  flex-wrap: wrap;
}

.cta-button {
  padding: 12px 30px;
  font-size: 1rem;
  font-weight: 600;
  border: 2px solid white;
  background: transparent;
  color: white;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.cta-button:hover {
  background: white;
  color: #667eea;
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.cta-button.secondary {
  background: rgba(255, 255, 255, 0.2);
  border-color: rgba(255, 255, 255, 0.5);
}

.cta-button.secondary:hover {
  background: rgba(255, 255, 255, 0.3);
  border-color: white;
}

.intro-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  margin: 20px 0;
  border: 4px solid white;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.social-link {
  position: fixed;
  right: 30px;
  top: 30px;
  color: white;
  text-decoration: none;
  transition: all 0.3s ease;
  z-index: 100;
}

.social-link:hover {
  transform: scale(1.2) rotate(5deg);
}

.social-link.instagram:hover {
  color: #E1306C;
}

@media (max-width: 768px) {
  .intro-name {
    font-size: 2.5rem;
  }

  .intro-image {
    width: 150px;
    height: 150px;
  }

  .intro-major {
    font-size: 1.2rem;
  }

  .intro-hook {
    font-size: 1rem;
  }

  .intro-cta {
    flex-direction: column;
    gap: 15px;
  }

  .cta-button {
    width: 100%;
  }
}
